import React, { Component } from 'react'
import Slide from "react-slick";
import { hostUrl } from '../store/config'

import withReducer from "../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../store/reducers";
import * as Actions from "../store/actions";

class Slider extends Component {

  constructor(props)
  {
    super(props);

    this.state = {
      bannerData: []
    };
  }

  static getDerivedStateFromProps(props, state) {
    return {
      bannerData: props.bannerData
    };
  }

  componentDidMount() {
    this.props.getBanner();
  }

  render() {
    var settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 2000,
    };

    const sliderImage =[
      {
        alt: 'Slide 1',
        path: '/assets/images/slider/slide1.jpg',
        bgPath: '/assets/images/slider/slide1-bg.jpg',
        bgColor: '#6abadf',
        link: '#'
      },
      {
        alt: 'Slide 2',
        path: '/assets/images/slider/slide2.jpg',
        bgPath: '',
        bgColor: 'rgb(99, 217, 224)',
        link: '#'
      }
    ];
    // console.log('Banners :: ' + this.state.bannerData);
    return (
      <div>
        {/* <div className="spinner__container spinner__effect--8">
          <ul className="spinner">
            <li className="spinner__square"></li>
            <li className="spinner__square"></li>
            <li className="spinner__square"></li>
            <li className="spinner__square"></li>
            <li className="spinner__square"></li>
            <li className="spinner__square"></li>
            <li className="spinner__square"></li>
            <li className="spinner__square"></li>
            <li className="spinner__square"></li>`
          </ul>
        </div> */}
        <Slide {...settings} className="home-slider">
          { this.state.bannerData && this.state.bannerData.map((img) => (
            <div key = {img.id}>
              <a href={img.banner_link}>
                <div className="slide" 
                  style={{
                    backgroundColor: img.banner_bg_color,
                    backgroundImage: `url(${hostUrl + img.banner_bg_image_path})`
                  }}
                >
                  <img src={hostUrl + img.banner_image_path} alt={img.banner_alt} />
                </div>
              </a>
            </div>
            ))
          }
            
          </Slide>
        </div>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  // console.log('Banners :: ' + JSON.stringify(state.banners.banner.bannerData));
  return {
    bannerData: state.banners.banner.bannerData
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators({
    getBanner: Actions.getBanner
  }, dispatch)
}

export default withReducer('banners', reducer)
                  (
                    connect(mapStateToProps,mapDispatchToProps)(Slider)                    
                  )

