import React, { Component } from 'react';
import Slider from './Slider';
import PremiumBoutiques from './PremiumBoutiques';
import SpringSummer from './SpringSummer';
import PricePointStore from './PricePointStore';
import InternationalTrends from './InternationalTrends';
import BestBrands from './BestBrands';
import BabyCorner from './BabyCorner';
import PickYourStyle from './PickYourStyle';
import HomeBottom from './HomeBottom';
import Header from '../MainHeader';
import Footer from '../Footer';
import {Helmet} from 'react-helmet'

// ... REDUX ...
import withReducer from "../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../store/reducers";
import * as Actions from "../store/actions";
class Home extends Component {

  componentDidMount() {
    this.props.getHomeSEO();
  }

  static getDerivedStateFromProps(props, state) {
    return {
      home_seo: props.home_seo
    }
  }

  render() {
    return (
        <div>
          {/* <Header /> */}
          <Helmet>
              <title>{`${this.state.home_seo ? this.state.home_seo.seo_title : 'Online Shopping'} - Baby nest Boutique`} </title>
              <meta name="description" content={this.state.home_seo && this.state.home_seo.seo_desc} />
              <meta name="keywords" content={this.state.home_seo && this.state.home_seo.seo_tags} />
          </Helmet>
          <Slider />
          <PremiumBoutiques />
          <SpringSummer />
          <PricePointStore />
          <InternationalTrends />
          <BestBrands />
          <BabyCorner />
          <PickYourStyle />
          <HomeBottom />
          {/* <Footer /> */}
        </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
      home_seo: state.home.header.home_seo,
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators({
      getHomeSEO: Actions.getHomeSEO
  }, dispatch)
}

export default withReducer('home', reducer)(connect(mapStateToProps, mapDispatchToProps)(Home))
// export default Home;