import React, { Component } from 'react'
import { hostUrl } from './store/config'
import Slide from "react-slick";

// ... REDUX ...
import withReducer from "./../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./store/reducers";
import * as Actions from "./store/actions";

class TopBrands extends Component {

    constructor(props)
    {
        super(props);

        this.state = {
            settings:{}
        };
    }

    componentDidMount() {
        this.props.getLogos()
    }

    static getDerivedStateFromProps(props, state)
    {
        // console.log('Logos :: ' + JSON.stringify(props));
        return {
        settings: props.settings,
        logoData: props.logoData,
        }
    }

    render() {
        return (<Slide {...this.state.settings}>
            {this.state.logoData && this.state.logoData.map((img) => (
                <div key = {Math.random()}>
                    <a href={hostUrl + img.logo_link}>
                    <div className="slide">
                        <img width='100%' height='80px' src={hostUrl + img.logo_link} alt={"Brands"} />
                    </div>
                    </a>
                </div>
            ))} 
            </Slide>)
    }
}

const mapStateToProps = (state, ownProps) => {
    // console.log('state Logo :: ' + state.brands.logo.logoData);
    return {
      logoData: state.brands.logo.logoData,
      slidesToShow: state.brands.logo.slidesToShow
    }
  }
  
  const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
      getLogos: Actions.getLogos
    }, dispatch)
  }

export default withReducer('brands', reducer)(connect(mapStateToProps,mapDispatchToProps)(TopBrands))